.itemList {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 20px;
    .item {
        box-sizing: border-box;
        width: 240px;
        border: 1px solid #602fae27;
        border-radius: 25px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        padding: 15px 20px;
        margin-bottom: 20px;
        &-header {
            font-size: 16px;
            text-align: right;
            margin: 10px 0;
        }
        &-cost {
            text-align: right;
            margin-bottom: 10px;
            font-size: 14px;
        }
        img {
            transition: 0.3s;
            width: 100%;
        }
        &:hover {
            box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.08);
        }
        .buttons {
            display: flex;
            justify-content: space-between;
            align-items: center;
            button {
                background-repeat: no-repeat;
                background-position: center center;
                cursor: pointer;
            }

            button.basket {
                width: 60%;
                height: 40px;
                background-image: url('../../Assets/Images/basketwhite.svg');
                background-color: #8f4ef9;
                border: none;
                border-radius: 12px;
            }

            button.favorite {
                color: #8f4ef9;
                width: 24px;
                height: 24px;
                background-image: url('../../Assets/Images/heart.svg');
                background-color: #fff;
                background-size: contain;
                border: none;
            }
        }
    }
}
